import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  open: false,
  status: "idle"
};

export const chatWidgetSlice = createSlice({
  name: "chatWidget",
  initialState,
  reducers: {
    toggle: (state) => {
      state.open = !state.open;
    }
  }
});

export const { toggle } = chatWidgetSlice.actions;

export default chatWidgetSlice.reducer;
