import React, { useEffect, useState } from "react";
import Sidebar from "./components/sidebar";
// import { createRoot } from "react-dom/client";
// import ChatWidget from "./components/chat-widget/chat-widget";
// import NavItem from "./components/nav-item";
// import PopIn from "./components/pop-in/pop-in";

function App() {
  const [vueState, setVueState] = useState({});
  useEffect(() => {
    setVueState(window?.app?.__vue__?.$store?.state);
    console.log(
      "IMG PLUGIN VUE STATE UPDATED",
      window.app.__vue__.$store.state
    );
    return () => {
      setVueState({});
    };
  }, [window.app]);

  return (
    <div>
      <Sidebar vueState={vueState} />
    </div>
  );
}

export default App;
